@import url("https://fonts.googleapis.com/css2?family=Karla:wght@400;700&display=swap");
/*! minireset.css v0.0.6 | MIT License | github.com/jgthms/minireset.css */
html,
body,
p,
ol,
ul,
li,
dl,
dt,
dd,
blockquote,
figure,
fieldset,
legend,
textarea,
pre,
iframe,
hr,
h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0;
  padding: 0;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-size: 100%;
  font-weight: normal;
}

ul {
  list-style: none;
}

button,
input,
select {
  margin: 0;
}

html {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

img,
video {
  height: auto;
  max-width: 100%;
}

iframe {
  border: 0;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

td,
th {
  padding: 0;
}

:root {
  /* Colors */
  --clr-cyan: #2ab2af;
  --clr-light-cyan: #4bbebc;
  --clr-bright-yellow: #c0df34;
  --clr-light-gray: #e5eff5;
  --clr-grayish-blue: #98a6bd;
  --clr-white: white;
  /* Fonts Families */
  --ff-heading: "Karla", sans-serif;
  --ff-body: "Karla", sans-serif;
  /* Fonts Sizes */
  --fs-title: 1.2rem;
  --fs-body: 1rem;
  /* Fonts Weight */
  --fw-thin: 100;
  --fw-extra-light: 200;
  --fw-light: 300;
  --fw-regular: 400;
  --fw-medium: 500;
  --fw-semi-bold: 600;
  --fw-bold: 700;
  --fw-extra-bold: 800;
  --fw-black: 900;
}
@media only screen and (min-width: 1440px) {
  :root {
    --fs-title: 1.45rem;
    --fs-body: 1rem;
  }
}

body {
  position: relative;
  background-color: #e5eef6;
}

:root {
  font-size: 100%;
}

body {
  font-family: var(--ff-body);
  font-size: var(--fs-body);
  font-weight: var(--fw-regular);
  color: var(--clr-white);
  letter-spacing: -0.2px;
}

h1,
h2 {
  font-family: var(--ff-heading);
  font-weight: var(--fw-bold);
  line-height: 1.4;
}

h1 {
  font-size: var(--fs-title);
  letter-spacing: 0.2px;
  margin-bottom: 20px;
}

h2 {
  font-size: 1.125rem;
  margin-bottom: 18px;
}

.site {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
}

.site-content {
  position: relative;
  flex-grow: 1;
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
}

.btn {
  white-space: nowrap;
  vertical-align: middle;
  user-select: none;
  text-decoration: none;
  text-align: center;
  padding: 0.75em;
  line-height: 1.5;
  font-weight: 700;
  font-size: 1rem;
  display: inline-block;
  cursor: pointer;
  border: 1px solid transparent;
  border-radius: 0.375rem;
}

.price-card {
  display: flex;
  flex-direction: column;
  width: 100%;
  max-width: 310px;
  border-radius: 8px;
  overflow: hidden;
}
.price-card__section {
  padding: 24px;
}
.price-card__section--community {
  background-color: var(--clr-white);
  color: var(--clr-grayish-blue);
  font-size: 0.875rem;
  line-height: 1.8;
  padding-top: 30px;
  padding-bottom: 34px;
}
.price-card__section--subscription {
  background-color: var(--clr-cyan);
}
.price-card__section--why-us {
  background-color: var(--clr-light-cyan);
  font-size: 0.875rem;
  line-height: 1.4;
}
.price-card__title--community {
  color: var(--clr-cyan);
}
.price-card__highlighted {
  color: var(--clr-bright-yellow);
  margin-bottom: 14px;
  line-height: 1.3;
  letter-spacing: -0.3px;
  font-size: 1rem;
}
.price-card__price {
  display: flex;
  align-items: center;
  font-size: 1rem;
  color: var(--clr-light-gray);
  margin-bottom: 8px;
}
.price-card__price span {
  font-size: 32px;
  font-weight: 700;
  margin-right: 12px;
  line-height: 1;
  color: var(--clr-white);
}
.price-card__cta {
  display: block;
  color: var(--clr-white);
  margin-top: 1.75rem;
  background-color: var(--clr-bright-yellow);
  box-shadow: rgba(0, 0, 0, 0.25) 0 0 1rem;
}
@media only screen and (min-width: 1440px) {
  .price-card {
    display: grid;
    grid-template-columns: 1fr 1fr;
    max-width: 635px;
  }
  .price-card__section {
    padding: 38px 40px;
  }
  .price-card__section--community {
    grid-column: span 2;
    font-size: 1rem;
    padding-top: 40px;
    padding-bottom: 40px;
  }
  .price-card__highlighted {
    font-size: 1.19rem;
    margin-bottom: 0.375rem;
  }
}

.attribution {
  height: 20px;
  font-size: 0.6875rem;
  text-align: center;
  color: #333;
  position: fixed;
  bottom: 0;
  width: 100vw;
  left: 50%;
  transform: translateX(-50%);
}
.attribution a {
  color: #333;
}